:root {
    ---eigengrau: #16161D;
    ---body_fonts: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    ---title_fonts: Garamond, serif;
}

body {
    background-color: var(---eigengrau);
    font-family: var(---body_fonts);
    color: #fff;
    font-size: calc(10px + 1vh);;
}

a {
    color: #4545ff;
}

/* styling for the header / navigation section */

header {
    float: left;
    width: 40%;
    min-width: 390px;
    margin: 1%;
}

header fieldset {
    border: 4px solid #fff;
    box-shadow: 20px 20px 0px 10px rgba(0,0,0,1);
}

header legend {
    font-family: var(---title_fonts);
    font-size: calc(18px + 3vh);
}

header ul {
    list-style-type: none;
    background-color: rgba(255,255,255,0.05);
    padding: 0px;
    border-radius: 20px;
}

header li {
    border-radius: 20px;
}

header li:hover {
    border: 2px solid white;
    background-color: rgba(0,0,0,0.2);
}

.navlink {
    text-decoration: none;
    font-size: calc(5px + 3vh);
    color: #fff;
}

.innernavlink {
    padding: 1% 0px 1% 10%;
}

/* styling for the content sections */

#projectslistsection {
    clear: left;
    float: left;
    width: 40%;
    min-width: 390px;
    margin: 1%;
}

section fieldset {
    border: 4px solid #fff;
    box-shadow: 20px 20px 0px 10px rgba(0,0,0,1);
}

section legend {
    font-family: var(---title_fonts);
    font-size: calc(20px + 3vh);
}

section ul {
    list-style-type: none;
    background-color: rgba(255,255,255,0.05);
    padding: 0.5% 0px;
}

section li {
    border: 2px solid white;
    font-size: calc(5px + 2vh);
    padding: 1% 0px 1% 5%;
    margin: 2%;
}

.projectslihashlink {
    text-decoration: none;
    font-size: calc(5px + 3vh);
    color: #fff;
}

.rightdisplaysection {
    float: right;
    width: 45%;
    min-width: 390px;
    margin: 1%;
}

.rightdisplaysection legend {
    font-size: calc(15px + 2vh);
}

/* styling for images, primarily within project section */

img {
    display: block;
    border: 1px solid white;
    margin: 5px;
    padding: 5px;
}

#nea_car_demo {
    width: 30%;
    min-width: 225px;
    margin: auto;
}

#nea_login_demo {
    width: 20%;
    min-width: 125px;
    float: right;
}

#nea_extract_demo {
    max-width: 95%;
    margin: auto;
}